import { Component, ElementRef, HostBinding, HostListener, Input } from '@angular/core';

@Component({
    selector: 'bmo-ng-tab',
    template: `
        {{label}}
        <span class="tab-indicator"></span>
    `
})
export class BmoNgTabComponent {
    @Input() label = "";
    @Input() set active(val: boolean) {
        this.tabIndex = val ? 0 : -1;
        this.activeTabLabelClass = this.ariaSelectedAttribute = val;
    }

    @HostBinding('attr.id') id = 'tab-' + this.tabId;
    @HostBinding('attr.role') roleAttribute = 'tab';
    @HostBinding('attr.tabindex') tabIndex = -1;
    @HostBinding('attr.aria-selected') ariaSelectedAttribute = false;
    @HostBinding('class.tab') tabClass = true;
    @HostBinding('class.active-tab-label') activeTabLabelClass = false;

    @HostListener('keydown', ['$event'])
    private navigateTabs(event: KeyboardEvent) {
        switch (event.key) {
            case 'Left':
            case 'ArrowLeft':
                this.elementRef.nativeElement.previousElementSibling?.focus();
                break;
            case 'Right':
            case 'ArrowRight':
                this.elementRef.nativeElement.nextElementSibling?.focus();
                break;
            default:
                break;
        }
    }

    get tabId(): number {
        return this._tabId;
    }
    set tabId(val: number) {
        this.id = 'tab-' + val;
        this._tabId = val;
    }
    private _tabId: number;

    constructor(public elementRef: ElementRef) {
        this.tabId = 0;
    }

    selectTab(): void {
        this.active = true;
    }

    deselectTab(): void {
        this.active = false;
    }
}