import { AfterViewInit, Component, ElementRef, Input, OnInit } from '@angular/core';

@Component({
  selector: 'bmo-ng-checkbox',
  template: `
    <ng-container *ngIf="isChild">
        <div class="input-wrap input-checkbox">
          <input
            type="checkbox"
            name="{{name}}"
            id="{{id}}"
            class="check"
            [value]="value"
            [checked]="checked"
            [attr.aria-checked]="checked"
            [disabled]="disabled"
            (click)="toggleChecked()"
            (keyup)="setLastState()"
          >
          <label class="check-label active-label" for="{{id}}">{{label}}</label>
        </div>
    </ng-container>
    <ng-container *ngIf="!isChild">
        <input
        type="checkbox"
        name="{{name}}"
        id="{{id}}"
        class="check"
        [value]="value"
        [checked]="checked"
        [attr.aria-checked]="checked"
        [disabled]="disabled"
        (click)="toggleChecked()"
        (keyup)="setLastState()"
      >
      <label class="check-label active-label" for="{{id}}">{{label}}</label>
    </ng-container>
  `
})
export class BmoNgCheckboxComponent implements OnInit, AfterViewInit {

  @Input() id: string;
  @Input() label: string;
  @Input() value: string;
  @Input() name: string;

  @Input() checked = false;
  @Input() disabled = false;
  @Input() isChild = false;

  public lastState = false;

  constructor(private elementRef: ElementRef) { }

  ngOnInit(): void {
    if (!this.id) {
      const random = Math.round(Math.random() * 10000);
      this.id = this.label.replace(/\s+/g, '-').toLowerCase() + random;
    }
  }

  ngAfterViewInit() {
    if (this.isChild) {
      const oldParentElement = this.nativeElement.parentElement;

      const listElement = document.createElement('li');
      listElement.appendChild(this.nativeElement);

      oldParentElement.appendChild(listElement);
    }
  }

  toggleChecked() {
    this.checked = !this.checked;

    this.setLastState();
  }

  setChecked(val: boolean | string) {
    if (typeof val === 'boolean') {
      this.checked = val;
    } else {
      switch (val) {
        case 'true':
        case 'false':
            this.checked = Boolean(val);
            break;
        case 'last':
          this.checked = this.lastState || false;
          break;
        default:
            break;
      }
    }
  }

  private setLastState() {
    if (this.isChild) {
      this.lastState = this.checked;
    }
  }

  get nativeElement() {
    return this.elementRef.nativeElement as HTMLElement;
  }

  get inputElement() {
      return this.elementRef.nativeElement.firstElementChild as HTMLInputElement;
  }

}
