import { AfterViewInit, Component, ElementRef, HostBinding, Input, OnInit } from '@angular/core';

@Component({
    selector: 'bmo-ng-card-checkbox',
    template:  `
        <div class="bmo-card-checkbox check-fixed-cards">
            <input
                id="{{labelId}}"
                type="checkbox"
                class="card-check-input"
                [value]="value"
                [disabled]="disabled"
                [checked]="checked"
                [attr.aria-checked]="checked"
                (click)="toggleChecked()"
            >
            <label class="card-check-label" for="{{labelId}}">
                <span class="check"></span>
                <span class="card-check-image">
                    <img *ngIf="img" src="{{img}}" role="presentation" />
                </span>
                <span class="card-check-label-text">{{label}}</span>
            </label>
        </div>
    `,
    styles: [':host { display: inline-block; }']
})
export class BmoNgCardCheckboxComponent implements OnInit {
    @HostBinding('class.check-card') checkCardClass = true;

    @Input() id: string;
    @Input() label = "";
    @Input() img: string;
    @Input() value: string;

    @Input() disabled = false;
    @Input() checked = false;

    public labelId: string;
    constructor(public elementRef: ElementRef) {}

    ngOnInit() {
        if (this.id) {
            this.labelId = this.id;
        } else {
            const random = Math.round(Math.random() * 10000);
            this.labelId = this.label.replace(/\s+/g, '-').toLowerCase() + random;
        }
    }

    toggleChecked() {
        this.checked = !this.checked;
    }

    get inputElement() {
        return this.elementRef.nativeElement.firstElementChild as HTMLInputElement;
    }
}