import { Component, OnInit, ViewChild, ElementRef, Input, AfterViewInit, AfterViewChecked } from '@angular/core';

@Component({
  selector: 'bmo-ng-popover',
  templateUrl: './bmo-ng-popover.component.html',
  host: { '[class.popover-wrapper]': 'true'},
  styleUrls: ['./bmo-ng-popover.component.scss']
})
export class BmoNgPopoverComponent implements OnInit, AfterViewInit, AfterViewChecked {
  @Input() icon = 'enclosed-help';
  @Input() direction: 'top'|'left'|'right'|'bottom' = 'top';
  @Input() title = '';
  @Input() containerStyle;
  @Input() buttonStyle;

  @ViewChild('popoverButton') popoverButton: ElementRef;
  @ViewChild('popoverContent') popoverContent: ElementRef;

  public isActive: boolean;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
  }

  ngAfterViewChecked() {
    // Fixes BMO Design System bug where margin for top popover isn't dynamic with the popover height
    if (this.direction === 'top' && this.popoverContent) {
      this.popoverContent.nativeElement.style.marginTop = -(this.popoverContent.nativeElement.clientHeight + 30) + 'px';
    }
  }

  displayPopover() {
    this.isActive = true;

    setTimeout(() => {
      this.popoverContent.nativeElement.focus();
    }, 200);
  }

  closePopover() {
    this.isActive = false;
  }
}
