import { AfterContentInit, AfterViewInit, Component, ContentChildren, ElementRef, HostListener, Input, OnInit, Optional, QueryList, Self, ViewChild } from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { BmoNgCheckboxComponent } from './bmo-ng-checkbox.component';

@Component({
    selector: 'bmo-ng-parent-checkbox',
    template: `
        <div class="input-wrap input-checkbox parent-checkbox">
            <input #inputElement
                type="checkbox"
                name="{{name}}"
                id="{{id}}"
                [checked]="checked"
                [disabled]="disabled"
                [attr.aria-checked]="ariaState"
                [attr.aria-controls]="childIds"
                (click)="onClick($event)"
                (keydown)="onKeyDown($event)"
            >
            <label for="{{id}}">{{label}}</label>
        </div>
        <ul class="checkboxes">
            <ng-content select="bmo-ng-checkbox"></ng-content>
        </ul>
    `
})
export class BmoNgParentCheckboxComponent implements AfterContentInit, AfterViewInit, OnInit, ControlValueAccessor {
    @ContentChildren(BmoNgCheckboxComponent) checkboxes: QueryList<BmoNgCheckboxComponent>;
    @ViewChild('inputElement') inputElement: ElementRef;

    @Input() id: string;
    @Input() label: string;
    @Input() errorMessage: string;
    @Input() name = 'parent-checkbox-group-' + this.randomInterval(0, 1000);

    // States
    @Input() get disabled() {
        return this._disabled;
    }
    set disabled(disabled: boolean) {
        this._disabled = disabled;

        if (this.checkboxes) {
            for (const checkbox of this.checkboxes) {
                checkbox.disabled = disabled;
            }
        }
    }
    private _disabled = false;
    @Input() checked = false;

    public childIds: string;
    public ariaState: 'false' | 'true' | 'mixed' = 'false';

    get error(): boolean {
        return this.ngControl ? (this.ngControl.invalid && this.ngControl.dirty) : false;
    }

    onChange = (val: Array<string>) => {};
    onTouched = () => {};

    constructor(@Optional() @Self() private ngControl: NgControl) {
        if (ngControl) {
            ngControl.valueAccessor = this;
        }
    }

    ngOnInit() {
        if (!this.id) {
            this.id = this.name;
        }
    }

    ngAfterContentInit() {
        const childIds = [];

        for (const checkbox of this.checkboxes) {
            checkbox.isChild = true;
            checkbox.disabled = this.disabled;
            checkbox.name = this.name;
            childIds.push(checkbox.id);
        }

        this.childIds = childIds.join(' ');
    }

    ngAfterViewInit() {
        for (const checkbox of this.checkboxes) {
            checkbox.disabled = this.disabled;

            checkbox.inputElement.removeEventListener('click', this._onClick);
            checkbox.inputElement.addEventListener('click', this._onClick);
        }

        this.updateParentCheckbox();
    }

    writeValue(obj: any): void {
        if (!this.checkboxes) {
            return this.onChange(null);
        }

        const checkboxValues = [];
        for (const checkbox of this.checkboxes) {
            if (checkbox.checked) {
                checkboxValues.push(checkbox.value);
            }
        }

        if (checkboxValues.length === 0) {
            this.onChange(null);
        }

        this.onChange(checkboxValues);
    }
    registerOnChange(fn: any): void {
        this.onChange = fn;
    }
    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }
    setDisabledState?(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

    public updateParentCheckbox() {
        const count = this.checkboxes.reduce((acc, curr) => curr.checked ? acc + 1 : acc, 0);

        switch (count) {
            case 0:
                this.setChecked('false');
                break;
            case this.checkboxes.length:
                this.setChecked('true');
                break;
            default:
                this.setChecked('mixed');
                this.updateControlledStates();
                break;
        }
    }

    public toggleParentCheckbox() {
        switch (this.ariaState) {
            case 'false':
            case 'mixed':
                this.setCheckboxes(true);
                break;
            case 'true':
                this.setCheckboxes(false);
                break;
            default:
                break;
        }
    }

    private setChecked(state: 'false' | 'true' | 'mixed') {
        this.ariaState = state;
        this.inputElement.nativeElement.checked = this.checked = state === 'false' ? false : true;
    }

    private setCheckboxes(val: boolean) {
        for (const checkbox of this.checkboxes) {
            checkbox.setChecked(val);
        }
        this.updateParentCheckbox();
    }

    private updateControlledStates() {
        for (const checkbox of this.checkboxes) {
            checkbox.lastState = checkbox.checked;
        }
    }

    private anyLastChecked() {
        const count = this.checkboxes.reduce((acc, curr) => curr.lastState ? acc + 1 : acc, 0);
        return count > 0;
    }

    _onClick = (event) => {
        this.updateParentCheckbox();
        this.writeValue('');
        event.stopPropagation();
    }

    onClick(event: MouseEvent) {
        this.toggleParentCheckbox();
        this.writeValue('');
        event.stopPropagation();
    }

    onKeyDown(event: KeyboardEvent) {
        if (event.key === 'Space') {
            this.toggleParentCheckbox();
            event.stopPropagation();
            event.preventDefault();
        }
    }

    private randomInterval(min: number, max: number): number {
        return Math.floor(Math.random() * (max - min + 1) + min);
    }
}