import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, HostBinding } from '@angular/core';

@Component({
  selector: 'bmo-ng-accordion',
  templateUrl: './bmo-ng-accordion.component.html',
  styleUrls: ['./bmo-ng-accordion.component.scss']
})
export class BmoNgAccordionComponent implements OnInit, AfterViewInit {
  @ViewChild('accordionTrigger') accordionTrigger: ElementRef;
  @ViewChild('accordionPanel') accordionPanel: ElementRef;
  @ViewChild('panelContainer') panelContainer: ElementRef;
  @ViewChild('panelContent') panelContent: ElementRef;

  @HostBinding('class.bmo-accordion') bmoAccordionClass = true;
  @HostBinding('attr.icon-right') iconRightAttribute = null;
  @HostBinding('attr.compact') compactAttribute = null;

  @Input() title = '';
  @Input() get compact() {
    return this._compact;
  }
  set compact(compact: boolean) {
    this.compactAttribute = compact;
    this._compact = compact;

    if (!this.compactAttribute) {
      this.compactAttribute = null;
    }
  }
  _compact = false;
  @Input() get iconRight() {
    return this._iconRight;
  }
  set iconRight(iconRight: boolean) {
    this.iconRightAttribute = iconRight;
    this._iconRight = iconRight;

    if (!this.iconRightAttribute) {
      this.iconRightAttribute = null;
    }
  }
  _iconRight = false;
  @Input() get expanded() {
    return this._expanded;
  }
  set expanded(expanded: boolean) {
    this._expanded = expanded;
    this.setPanelContainerHeight();
  }
  _expanded = false;
  @Input() id;

  constructor() {}

  ngOnInit() {
    if (!this.id) {
      this.id = "accordion";
    }
  }

  ngAfterViewInit() {
    this.setPanelContainerHeight();
  }

  toggleAccordion(): void {
    this.expanded = !this.expanded;
  }

  keydownShouldToggleAccordion(event: KeyboardEvent): void {
    if (event.key === ' ' || event.key === 'Spacebar' || event.key === 'Enter') {
      this.toggleAccordion();
      event.preventDefault();
    }

    if (event.key === 'Down' || event.key === 'ArrowDown') {
      this.expanded = true;
      event.preventDefault();
    }
  }

  keyupShouldToggleAccordion(event: KeyboardEvent): void {
    if (event.key === 'Up' || event.key === 'ArrowUp') {
      this.expanded = false;
      event.preventDefault();
    }
  }

  private setPanelContainerHeight(): void {
    if (this.expanded) {
      this.panelContainer.nativeElement.style.height = this.panelContent.nativeElement.offsetHeight + 'px';
    } else {
      this.panelContainer.nativeElement.removeAttribute('style');
    }
  }

}
