import { Component, HostBinding, Input } from '@angular/core';

@Component({
    selector: 'bmo-ng-tab-panel',
    template: `
        <div class="panel-content">
            <ng-content></ng-content>
        </div>
    `
})
export class BmoNgTabPanelComponent {
    @Input() set active(val: boolean) {
        this.ariaHiddenAttribute = !val;
        this.activeTabContentClass = val;
    }

    @HostBinding('class.tab-panel') tabPanelClass = true;
    @HostBinding('attr.id') id = 'tab-' + this.tabId;
    @HostBinding('attr.role') roleAttribute = 'tabpanel';
    @HostBinding('attr.aria-describedby') ariaDescribedByAttribute = 'tab-' + this.tabId;
    @HostBinding('class.active-tab-content') activeTabContentClass = false;
    @HostBinding('attr.aria-hidden') ariaHiddenAttribute = true;

    get tabId(): number {
        return this._tabId;
    }
    set tabId(val: number) {
        this.id = 'tab-' + val + '-panel';
        this.ariaDescribedByAttribute = 'tab-' + val;
        this._tabId = val;
    }
    private _tabId: number;

    displayTabPanel(): void {
        this.active = true;
    }

    hideTabPanel(): void {
        this.active = false;
    }
}