import { AfterViewInit, Component, ElementRef, forwardRef, Input, OnInit, Optional, Self, ViewChild } from '@angular/core';
import { AbstractControl, ControlValueAccessor, NgControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';

@Component({
  selector: 'bmo-ng-select',
  templateUrl: './bmo-ng-select.component.html',
  styleUrls: ['./bmo-ng-select.component.scss']
})
export class BmoNgSelectComponent implements OnInit, AfterViewInit, ControlValueAccessor {
  @ViewChild('selectElement') selectElement: ElementRef;

  @Input() label = '';
  @Input() helperText = '';
  @Input() errorMessage = '';
  @Input() autofocus = false;

  // States
  @Input() disabled = false;
  @Input() readonly = false;
  @Input() mercury = false;

  public labelId: string;
  public value: string;

  onChange = (val: string) => {};
  onTouched = () => {};
  onValidatorChange = () => {};

  get error(): boolean {
    return this.ngControl ? (this.ngControl.invalid && this.ngControl.dirty) : false;
  }

  constructor(@Optional() @Self() private ngControl: NgControl) {
    if (this.ngControl) {
      this.ngControl.valueAccessor = this;
    }
  }

  ngOnInit(): void {
    this.labelId = this.label.replace(/\s+/g, '-').toLowerCase();
  }

  ngAfterViewInit(): void {
    if (this.autofocus) {
      this.selectElement.nativeElement.focus();
    }
  }

  writeValue(val: string): void {
    this.value = val;
    this.onChange(val);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}
