import { Component, OnInit, Input, Optional, Self, ViewChild, AfterViewInit, ElementRef } from '@angular/core';
import { NgControl, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'bmo-ng-text-field',
  templateUrl: './bmo-ng-text-field.component.html',
  styleUrls: ['./bmo-ng-text-field.component.scss']
})
export class BmoNgTextFieldComponent implements OnInit, AfterViewInit, ControlValueAccessor {
  @ViewChild('inputElement') inputElement: ElementRef;

  @Input() id = 'text-field-1';
  @Input() placeholder = "Placeholder";
  @Input() helperText: string;
  @Input() errorMessage = '';
  @Input() type: 'email' | 'hidden' | 'number' | 'password' | 'text' = 'text';
  @Input() maxLength: number;

  @Input() readonly = false;
  @Input() autofocus = false;
  @Input() mercury = false;
  @Input() disabled = false;

  public value = '';

  get error(): boolean {
    return this.ngControl ? (this.ngControl.invalid && this.ngControl.dirty) : false;
  }

  onChange = (val: string) => {};
  onTouched = () => {};

  constructor(@Optional() @Self() private ngControl: NgControl) {
    if (this.ngControl) {
      this.ngControl.valueAccessor = this;
    }
  }

  writeValue(obj: any): void {
    this.value = obj;
    this.onChange(obj);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    if (this.autofocus) {
      this.inputElement.nativeElement.focus();
    }
  }

}
