import { AfterContentInit, Component, ContentChildren, HostBinding, Input, OnDestroy, OnInit, Optional, QueryList, Self } from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { BmoNgRadioButtonComponent } from './bmo-ng-radio-button.component';

@Component({
  selector: 'bmo-ng-radio-button-wrap',
  templateUrl: './bmo-ng-radio-button-container.component.html',
  styleUrls: ['./bmo-ng-radio-button-container.component.scss']
})
export class BmoNgRadioButtonContainerComponent implements OnInit, OnDestroy, AfterContentInit, ControlValueAccessor {
  @HostBinding('class.radio-group') radioGroupClass = true;
  @ContentChildren(BmoNgRadioButtonComponent) radioButtons: QueryList<BmoNgRadioButtonComponent>;

  private radioGroupNum = this.randomInterval(0, 1000);

  @Input() legend: string;
  @Input() type: 'traditional' | 'box' = 'traditional';
  @Input() name = 'radio-group-' + this.radioGroupNum;
  @Input() errorMessage: string;

  // States
  @Input() disabled = false;

  get error(): boolean {
    return this.ngControl ? (this.ngControl.invalid && this.ngControl.dirty) : false;
  }

  onChange = (val: string) => {};
  onTouched = () => {};

  constructor(@Optional() @Self() private ngControl: NgControl) {
    if (this.ngControl) {
      this.ngControl.valueAccessor = this;
    }
  }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    const radioButtons = this.radioButtons.toArray();

    for (let i = 0; i < radioButtons.length; i++) {
      const radioButton = radioButtons[i];
      radioButton.name = this.name;
      radioButton.type = this.type;
      radioButton.disabled = this.disabled;

      if (!radioButton.id) {
        radioButton.id = 'radio-' + this.radioGroupNum + '-' + (i + 1);
      }

      if (!radioButton.value) {
        radioButton.value = String(i);
      }

      radioButton.inputElement.addEventListener('click', this._clickListener);
    }
  }

  ngOnDestroy() {
    for (const radioButton of this.radioButtons) {
      radioButton.inputElement.removeEventListener('click', this._clickListener);
    }
  }

  writeValue(obj: any): void {
    if (!this.radioButtons) {
      this.onChange(obj);
      return;
    }

    for (const radioButton of this.radioButtons.toArray()) {
      if (radioButton.inputElement.checked) {
        this.onChange(radioButton.value);
        return;
      }
    }

    this.onChange(obj);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  private _clickListener = (event) => {
    this.writeValue('');
    event.stopPropagation();
  }
  private randomInterval(min: number, max: number): number {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
}
