import { AfterContentInit, AfterViewInit, Component, ContentChildren, Input, OnDestroy, Optional, QueryList, Self } from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { BmoNgCheckboxComponent } from './bmo-ng-checkbox.component';

@Component({
    selector: 'bmo-ng-checkbox-wrap',
    template: `
        <div class="check-group">
            <fieldset class="input-wrap input-checkbox" [disabled]="disabled">
                <legend class="legend check-legend">{{label}}</legend>
                <ng-content select="bmo-ng-checkbox"></ng-content>
                <span *ngIf="error" class="error-message has-error" role="alert">{{errorMessage}}</span>
            </fieldset>
        </div>
    `
})
export class BmoNgCheckboxWrapComponent implements OnDestroy, AfterContentInit, AfterViewInit, ControlValueAccessor {
    @ContentChildren(BmoNgCheckboxComponent) checkboxes: QueryList<BmoNgCheckboxComponent>;

    @Input() label: string;
    @Input() errorMessage: string;
    @Input() name = 'checkbox-group-' + this.randomInterval(0, 1000);

    // States
    @Input() 
    get disabled() {
        return this._disabled;
    }
    set disabled(disabled: boolean) {
        this._disabled = disabled;

        if (this.checkboxes) {
            for (const checkbox of this.checkboxes) {
                checkbox.disabled = this.disabled;
            }
        }
    }
    private _disabled = false;

    onChange = (val: Array<string>) => {};
    onTouched = () => {};

    get error(): boolean {
        return this.ngControl ? (this.ngControl.invalid && this.ngControl.dirty) : false;
    }

    constructor(@Optional() @Self() private ngControl: NgControl) {
        if (ngControl) {
            ngControl.valueAccessor = this;
        }
    }

    ngAfterContentInit(): void {
        for (const checkbox of this.checkboxes) {
            checkbox.name = this.name;
        }
    }

    ngAfterViewInit() {
        for (const checkbox of this.checkboxes) {
            checkbox.inputElement.addEventListener('click', this._clickListener);
        }
    }

    ngOnDestroy() {
        for (const checkbox of this.checkboxes) {
            checkbox.inputElement.removeEventListener('click', this._clickListener);
        }
    }

    writeValue(obj: any): void {
        if (!this.checkboxes) {
            return this.onChange(null);
        }

        const checkboxValues = [];
        for (const checkbox of this.checkboxes) {
            if (checkbox.checked) {
                checkboxValues.push(checkbox.value);
            }
        }

        if (checkboxValues.length === 0) {
            return this.onChange(null);
        }

        return this.onChange(checkboxValues);
    }

    registerOnChange(fn: any): void {
        this.onChange = fn;
    }
    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }
    setDisabledState?(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

    private _clickListener = (event) => {
        this.writeValue('');
        event.stopPropagation();
    }

    private randomInterval(min: number, max: number): number {
        return Math.floor(Math.random() * (max - min + 1) + min);
    }
}